/*Theme Name: Azeri*/
@import url('https://fonts.googleapis.com/css2?family=Yanone+Kaffeesatz:wght@300;400;700&display=swap');
@import url('http://allfont.ru/allfont.css?fonts=president-cyr');
$cg: #5a8b5a;
$cr: #850505;
$cdg: #024807;
$t2: 2%;
$t5: 5%;
$t10: 10%;
$t15: 15%;

@mixin pd {
  padding: 0;
  margin: 0; }

@mixin td {
  text-decoration: none;
  padding: 0; }

#top_scroll {
  background: url('assets/img/background_pr.jpg')  no-repeat center top {
    size: cover;
    attachment: fixed; } }

section {
  margin-bottom: $t2; }
#main_logo,#contacts,#events,#azeri {
  margin-top: $t2; }

body {
  @include pd;
  color:#434243 {}
  font: {
    family: 'Yanone Kaffeesatz', sans-serif;
    weight: 400;
    size: 1.5rem; } }

#ul_miss {
        li {
          font-size: 2rem; } }

.nav_foot {
          li {
            a {
              font-size: 2rem; } } }
header {
  font-weight: 700;
  font-size: 2rem;
  background: rgba(196, 247, 231, 0.7); }

.carousel {
  margin: 0;
  height: 450px !important;
  &-inner {
    img {
      height: 400px !important;
      margin: $t5;
      margin-left: 0; } } }

li {
  margin: 0 $t5; }
.navbar {
  margin: 0 $t15;
  &-light {
    .navbar-nav {
      li {
        .nav-link {
          color: $cg;
          @include td;
          &-active, &:hover {
            color: $cr;
            @include td; } } } } } }

.fab, .fas, .nav_foot>li>a {
  color: $cg;
  @include td; }

#menu-item-language {
  &-ru, &-uk, &-en, &-az {
    list-style-type: none;
    @include pd;
    display: block;
    margin-right: $t2;
    font-size: 1.2rem;
    margin-top: 2%;
    &>a {
      color: #b2970a;
      &:hover {
        color: #005574; } }
    &>.nav-link-active {
      color: #005574; } } }


.card {
  background: rgba(196, 247, 231, 0.7);
  height: 200px;
  vertical-align: middle;
  margin-bottom: $t5; }

.btn-primary {
  background-color: $cg;
  border: none; }
.container {
   background: rgba(255, 255, 255, 0.8); }
h1, h2, h3 {
  font: {
    family: 'President Cyr', arial;
    size: 2.5rem;
    weight: bold; }
  color: $cr;
  margin-left: $t5;
  margin-top: $t2;
  text-align: center; }

h2, h3 {
  font-size: 2rem;
  text-align: left;
  color: $cdg;
  padding-top: $t2; }
h3 {
 font-size: 1.8rem; }

#img_big_logo, h1 {
  display: block;
  margin-left: auto;
  margin-right: auto; }

#img_big_logo {
  height: 200px; }

.container, footer {
  opacity: 0; //Элемент полностью прозрачный (невидимый)
  transition: 2s; //Скорость перехода состояния элемента
  animation: show 3s 1 { //Указываем название анимации, её время и количество повторов
    fill-mode: forwards; //Чтобы элемент оставался в конечном состоянии анимации
    delay: 0s; } } //Задержка перед началом

.fab {
  padding: 3%;
  font-size: 5rem; }

.fas {
  font-size: 3rem;
  margin-right: $t10; }

#social_fab {
  text-align: center; }

hr {
  color: $cdg;
  background-color: $cdg;
  height: 2px;
  width: 80%;
  margin: 0 {
    bottom: $t2; } }

#ul_miss {
  li {
    padding-bottom: $t2; } }

.social>ul>li, .nav_foot>li, #ul_miss>li {
  list-style-type: none; }

footer {
  margin-bottom: 7%; }

/*Контакты*/
#contacts>div>.row {
  padding: $t2 $t15; }

.podr {
  font-size: 2rem; }

.popmake-154 {
  width: 50%; }

.email_p {
  color: #007676;
  font-size: 1.5rem;
  padding: $t2 {
    left: $t15; } }

#mess_send {
  height: 150px; }

#email1 {
  height: 3rem; }
#mess_send, #email1 {
  max-width: 90%; }
#sub_mess {
  color: white;
  background: #007676;
  border: none {
    radius: 10px; }
  padding: $t2 $t5;
  justify-content: center;
  margin-left: 20%; }

/*События*/
.card>img {
  max-width: 200px; }

.event_row {
  padding: $t2; }
.event {
  height: 100%;
  margin: auto {
    bottom: 3%; } }


.img_fl {
  margin: 3%;
  width: 100%; }
.img_fl_a {
  width: 60%;
  margin-bottom: 2%; }

.img_hi {
  max-width: 300px;
  margin: 2% {
    bottom: 10%; } }
.dv_flag {
  display: flex;
  align-items: center;
  justify-content: center; }

.dv_flag p {
  margin: 0; }

/*Люди/Азейбарджан*/
.fa-arrow-up {
  position: fixed;
  top: 90vh;
  right: 0.5vw;
  z-index: 9999; }
#cont>li>a {
  font-size: 2rem;
  @include td;
  color:#434243 {} }
.his_img {
  text-align: center; }
#history>.container, #ukraine>.container, #baku>.container {
  padding: 2% {
    top: 0; } }
.img_uk_az {
  float: left;
  margin: 2%;
  max-width: 310px; }

@keyframes show {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@media screen and (max-width: 812px) {
  h1,h2 {
    font-size: 2rem; }
  #menu-item-language {
    &-ru, &-uk, &-en, &-az {
      &>a {
          @include pd;
          font-size: 1rem; } } } }

@media screen and (max-width: 568px) {
  h1, h2, .fio {
    font-size: 1.5rem; } }

@media only screen and (max-width : 768px) and (orientation: landscape) {
  #img_big_logo {
    height: 150px;
    margin-top: 1%; } }

@media only screen and (min-width : 1601px) and (orientation: landscape) {
  #ul_miss>li {
    font-size: 2.5rem; } }

@media only screen and (max-width : 320px) {
  .card {
    height: 220px; } }

@media only screen and (min-width : 1024px) and (max-width:1366px) {
  form {
    margin-left: 20%; }
  .navbar-light .navbar-nav li {
    margin: $t2; }
  input {
    margin-bottom: $t5; } }











